// export default StudentForm;

"use client";
import { useState } from 'react';

export default function StudentForm({ onAdd }) {
  const [form, setForm] = useState({
    firstName: '',
    lastName: '',
    dob: '',
    grade: ''
  });

  const [errors, setErrors] = useState({});

  const validate = () => {
    const newErrors = {};

    if (!form.firstName.trim()) {
      newErrors.firstName = 'First name is required';
    }

    if (!form.lastName.trim()) {
      newErrors.lastName = 'Last name is required';
    }

    if (!form.dob || isNaN(Date.parse(form.dob))) {
      newErrors.dob = 'Valid date of birth is required';
    }

    if (!form.grade || isNaN(form.grade) || form.grade < 1 || form.grade > 12) {
      newErrors.grade = 'Grade must be a number between 1 and 12';
    }

    setErrors(newErrors);

    return Object.keys(newErrors).length === 0;
  };

  const handleChange = (e) => {
    setForm({
      ...form,
      [e.target.name]: e.target.value
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (validate()) {
      onAdd({ ...form, id: Date.now() });
      setForm({ firstName: '', lastName: '', dob: '', grade: '' });
      setErrors({});
    }
  };

  return (
    <form onSubmit={handleSubmit} style={{ marginTop: '1rem' }}>
      <div>
        <input
          name="firstName"
          placeholder="First Name"
          value={form.firstName}
          onChange={handleChange}
        />
        {errors.firstName && <span style={{ color: 'red' }}>{errors.firstName}</span>}
      </div>

      <div>
        <input
          name="lastName"
          placeholder="Last Name"
          value={form.lastName}
          onChange={handleChange}
        />
        {errors.lastName && <span style={{ color: 'red' }}>{errors.lastName}</span>}
      </div>

      <div>
        <input
          name="dob"
          type="date"
          value={form.dob}
          onChange={handleChange}
        />
        {errors.dob && <span style={{ color: 'red' }}>{errors.dob}</span>}
      </div>

      <div>
        <input
          name="grade"
          placeholder="Grade"
          value={form.grade}
          onChange={handleChange}
        />
        {errors.grade && <span style={{ color: 'red' }}>{errors.grade}</span>}
      </div>

      <button type="submit" style={{ marginTop: '0.5rem' }}>Add Student</button>
    </form>
  );
}
